<script>
  import { util, xstate, Escape } from 'dmt-js';

  export let store;
  export let session;

  $: connected = $store.connected;
  $: controller = $store.controller;
  //$: pubkey = $store.controller && $store.controller.pubkey;
  $: player = $store.player;
  $: nearbyDevices = $store.nearbyDevices ? Object.values($store.nearbyDevices).filter(device => device.hasGui).sort(util.compareValues('deviceId')) : [];

  $: clientPubkey = $session.publicKeyHex;
  $: sharedSecretHex = $session.sharedSecretHex;

  $: activeDeviceId = controller ? controller.deviceName : null;

  const usualDeviceList = ['kitchen', 'midroom', 'outside', 'living-room', 'tv', 'dpanel', 'lab'];

  let searchTerms;

  // METHODS :::

  function searchInputChanged() {
    console.log(searchTerms);
  }

  function action(action, payload) {
    console.log(`Action: ${action}`);

    store
      .remoteObject('gui')
      .call('action', { action, storeName: 'player', payload })
      .catch(console.log);
  }

  function switchDevice(device) {
    localStorage.setItem('current_device_ip', device.ip);
    store.switch(device);
  }

  let playerMoreVisible = false;

  function toggleMore() {
    playerMoreVisible = !playerMoreVisible;
  }
</script>

<main>

  <Escape />

  <p>{connected ? '' : 'Disconnected ✖'}</p>

  <!-- <input bind:value={searchTerms} on:keyup={searchInputChanged} on:paste={searchInputChanged}> -->

  <!-- <p>Client pubkey: {clientPubkey}</p>
  <p>Shared secret: {sharedSecretHex}</p> -->

  {#if connected && controller}
    <!-- <p>{deviceIp}</p> -->

    <h2>{controller.deviceName}</h2>
    <!-- <p>Pubkey: {controller.pubkey}</p> -->

    {#if player}

      <div class="player_media section">

        {#if player.currentMedia && player.currentMedia.song && !player.paused}
          <p class="current_media">{player.currentMedia.mediaType == 'music' ? '♫' : '▶'} {player.currentMedia.artist ? `${player.currentMedia.artist} - ${player.currentMedia.song}` : player.currentMedia.song}</p>
        {:else}
          <p><b>Currently not playing</b></p>
        {/if}

        <p>Volume: {player.volume}</p>

      </div>

      <div class="player_controls section">

        {#if player.paused}
          <button on:click={() => action('play')} class="play">▶ Play </button>
        {:else}
          <button on:click={() => action('pause')} class="pause">● Pause</button>
        {/if}

        <button on:click={() => action('volume_down')} class="volume">Vol ↓</button>
        <button on:click={() => action('volume_up')} class="volume">Vol ↑</button>

        <button on:click={() => toggleMore()} class="more" class:bold={playerMoreVisible}>More</button>

      </div>

      <div class="player_more section" class:visible={playerMoreVisible}>

        <button on:click={() => action('limit')} class="limit">Limit {player.limit || ''}</button>
        {#if player.limit > 0}
          <button on:click={() => action('remove_limit')} class="remove_limit">Remove</button>
        {/if}

        <button on:click={() => action('next')} class="next">→ Next</button>
        <button on:click={() => action('shuffle')} class="shuffle">Shuffle</button>
        <button on:click={() => action('repeat')} class="repeat">Repeat {player.repeatCount || ''}</button>
      </div>

    {/if}

    {#if nearbyDevices}
      <div class="nearby_devices section">
        {#each nearbyDevices.filter(device => window.location.hostname != '192.168.0.60' || usualDeviceList.includes(device.deviceId)) as device}

          <button on:click={switchDevice(device)} class:active={device.deviceId == activeDeviceId}>

            {#if device.hasErrors}<span class='error'>!</span>{/if}

            {#if device.playing}
              {device.mediaType == 'music' ? '♫' : '▶'}
            {/if}

            {device.deviceId}
          </button>
        {/each}
      </div>
    {/if}

  {/if}

</main>

