	main {
		text-align: center;
		/*padding: 1em;*/
		/*max-width: 240px;*/
		margin: 0 auto;
	}

  .current_media {
    color: #3B419A;
  }

  .section {
    padding: 10px 10px;
  }

	.player_media {
    /*background-color: #AB73BF;*/
  }

  .player_controls {
    /*background-color: #6CA9BF;*/
  }

  .player_more {
    display: none;
  }

  .player_more.visible {
    display: block;
  }

  .nearby_devices {
    border-top: 1px solid #999;
    /*background-color: #BFB9AB;*/
  }

  button {
    margin-left: 10px;
    border-radius: 5px;
    cursor: pointer;
  }

  button:hover {
    opacity: 0.9;
  }

  button.active {
    border: 2px solid #3B419A;
  }

  button.more {
    /*background-color: #9AB5BF;*/
  }

  button.more.bold {
    font-weight: bold;
  }

  button.remove_limit {
    color: #777;
    background-color: white;
  }

  .error {
    background-color: #983128;
    color: white;
    padding: 2px 5px;
    margin-right: 5px;
  }

  p {
    padding: 0;
  }

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
</style>
